


















import {Component, Vue, Prop} from 'vue-property-decorator'

@Component 
export default class VTabs extends Vue{
    @Prop({required: true})
    tabs!: Array<any>
    
    @Prop({default: 0})
    value!: any

    switchTab(tab: any){
        this.$emit("input", tab)
    }

    isActive(tab: any){
        return tab == this.value
    }

    mounted(){
        if (this.tabs.length > 0) {
            this.switchTab(this.tabs[0])
        }
    }
}
