


























import { Component, Vue, Prop } from "vue-property-decorator";

@Component({
    inheritAttrs: false
})
export default class VInput extends Vue {
    @Prop({ type: String, required: false, default: "" })
    label!: string

    @Prop({ type: String, required: false, default: "" })
    error!: string
    
    @Prop({ type: String, required: false, default: "" })
    icon!: string

    @Prop({ required: false })
    value!: any
    
}
